.modalArea {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.563);
  z-index: 9999;
  top: 0;
  left: 0;
}

.modal {
  margin: 1em;
  padding: 1em;
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border-radius: 0.2em;
}

p {
  color: rgb(159, 151, 151);
}

.aviso {
  color: rgb(165, 42, 42);
}